/**
 * 
 *  API name
 */

interface WorldbookConfig {
  name: string; // 
  displayName: string; // 
  priority: number; // 
  keywords: string[]; // 
  autoLoad: boolean; // 
  description?: string; // 
  position?: 'before_char' | 'after_char'; // 
  depth?: number; // 
  fallbackEntries?: unknown[]; // 
}

interface LoadedWorldbook {
  name: string;
  displayName: string;
  entries: unknown[];
  fallback: boolean;
  error?: string;
  loadedAt: number;
}

interface WorldbookStatus {
  initialized: boolean;
  fallbackMode: boolean;
  characterWorldbook: string | null;
  loaded: Array<{
    name: string;
    displayName: string;
    entries: number;
    fallback: boolean;
    error?: string;
    loadedAt: string;
  }>;
  loading: Array<{
    name: string;
    displayName: string;
  }>;
  available: Array<{
    name: string;
    displayName: string;
    autoLoad: boolean;
  }>;
}

interface EventBus {
  on(event: string, callback: (data?: unknown) => void): void;
  emit(event: string, data?: unknown): void;
}

interface DetentionSystem {
  version: string;
  initialized: boolean;
  modules: Record<string, unknown>;
  config: Record<string, unknown>;
  state: Record<string, unknown>;
  events: EventBus;
  CacheManager: unknown;
  EventEmitter: unknown;
  ping(): boolean;
  registerModule(name: string, module: unknown): void;
  getModule<T = unknown>(name: string): T | undefined;
  handleError(error: unknown, context?: string): void;
}

